import { getDatabase, ref, set } from "firebase/database";
import { initializeApp } from "firebase/app";

const firebaseConfig = {
  apiKey: "AIzaSyCZSgaTUdWA9xKjmlsiY7wYryt4yiQeUys",
  authDomain: "bartlag.firebaseapp.com",
  databaseURL: "https://bartlag-default-rtdb.firebaseio.com",
  projectId: "bartlag",
  storageBucket: "bartlag.appspot.com",
  messagingSenderId: "131349116760",
  appId: "1:131349116760:web:1a8d3a8dd992beb996572d",
  measurementId: "G-P8Q5CE337R",
};

const app = initializeApp(firebaseConfig);
const db = getDatabase(app);

function Admin() {
  const resetCompletedIndices = () => {
    const completedRef = ref(db, "completedIndices");
    set(completedRef, null);

    const availableIndices = Array.from({ length: 22 }, (_, i) => i);
    const newIndices = [];
    while (newIndices.length < 4 && availableIndices.length > 0) {
      const randomIndex = Math.floor(Math.random() * availableIndices.length);
      const index = availableIndices[randomIndex];
      newIndices.push(index);
      availableIndices.splice(randomIndex, 1);
    }

    const dbRef = ref(db, "currentIndices");
    const updates = {
      indices: newIndices.join(","),
      lastUpdatedTime: new Date().getTime(),
    };
    set(dbRef, updates);
  };

  return (
    <div>
      <h1>Admin</h1>
      <button onClick={resetCompletedIndices}>Reset</button>
    </div>
  );
}

export default Admin;
