const app = initializeApp(firebaseConfig);
const analytics = getAnalytics(app);
const db = getDatabase(app);

function Home() {
  const [indices, setIndices] = useState([]);
  const [lastUpdatedTime, setLastUpdatedTime] = useState(null);
  const [currentTime, setCurrentTime] = useState(null);

  useEffect(() => {
    const dbRef = ref(db, "currentIndices");
    onValue(dbRef, (snapshot) => {
      const data = snapshot.val();
      if (data) {
        if (data.indices) {
          const newIndices = data.indices.split(",").map(Number);
          setIndices(newIndices);
        }
        if (data.lastUpdatedTime) {
          setLastUpdatedTime(data.lastUpdatedTime);
        }
      }
    });

    // Update current time every second
    const interval = setInterval(() => {
      setCurrentTime(new Date().getTime());
    }, 1000);

    return () => clearInterval(interval);
  }, []);

  const getTimeUntilNextUpdate = () => {
    if (!lastUpdatedTime) {
      return "N/A";
    }

    const currentTime = new Date().getTime();
    const timeDiff = currentTime - lastUpdatedTime;
    const seconds = Math.floor(timeDiff / 1000);
    const minutes = Math.floor(seconds / 60);
    const hours = Math.floor(minutes / 60);
    const days = Math.floor(hours / 24);

    const nextUpdate = new Date(lastUpdatedTime + 60 * 60 * 1000); // Add 1 hour to lastUpdatedTime
    const timeUntilNextUpdate = nextUpdate - currentTime;
    const nextUpdateMinutes = Math.floor(timeUntilNextUpdate / (1000 * 60));
    const nextUpdateSeconds = Math.floor((timeUntilNextUpdate / 1000) % 60);

    // Format minutes and seconds with leading zeros
    const formattedMinutes = String(nextUpdateMinutes).padStart(2, "0");
    const formattedSeconds = String(nextUpdateSeconds).padStart(2, "0");

    const formattedTime = `${formattedMinutes}:${formattedSeconds}`;
    return formattedTime;
  };

  const completeCard = (index, teamName) => {
    const completedRef = ref(db, "completedIndices");
    const newCompletedIndex = {
      index,
      teamName,
    };
    set(completedRef, newCompletedIndex);
  };

  return (
    <>
      <Box className="App-header mt_1">
        <img src={logo} className="App-logo mr_1" alt="logo mt_1" />

        <Typography variant="title" className="lg spm mt_1">
          BARTLAG
        </Typography>
      </Box>
      <Box className="tr_counter c_white mt_1">
        <Typography className="f_lg ff_md">
          {getTimeUntilNextUpdate()}
        </Typography>
      </Box>
      {/* <img src={logo} className="App-logo" alt="logo" /> */}
      <Box className="grid_container">
        <Box className="lg_w">
          {indices.map((index) => {
            const challenge = challenges[index];
            if (challenge) {
              return (
                <Box item xs={3} sm={3} md={3} lg={3} xl={3}>
                  <FlipCard
                    key={index}
                    name={challenge.name}
                    description={challenge.description}
                    distance={challenge.distance}
                    completeCard={completeCard}
                  />
                </Box>
              );
            }
            return null;
          })}
        </Box>
      </Box>
    </>
  );
}

export default Home;
